<template>
  <v-row class="ma-sm-4">
    <v-col cols="12">
      <v-menu
        ref="menu"
        v-model="menu"
        :close-on-content-click="false"
        :return-value.sync="dates"
        transition="scale-transition"
        offset-y
        min-width="290px"
      >
        <template v-slot:activator="{ on }">
          <v-text-field
            v-model="dateRangeText"
            solo
            flat
            hide-details="auto"
            background-color="#464646"
            label="Даты создания"
            readonly
            class="rounded-lg"
            v-on="on"
          >
            <template v-slot:prepend-inner>
              <v-icon class="mr-1">
                mdi-calendar
              </v-icon>
            </template>
          </v-text-field>
        </template>
        <v-date-picker
          v-model="dates"
          no-title
          scrollable
          range
        >
          <v-spacer />

          <v-btn
            text
            color="primary"
            @click="menu = false"
          >
            Отмена
          </v-btn>
          <v-btn
            text
            color="primary"
            @click="saveDates"
          >
            ОК
          </v-btn>
        </v-date-picker>
      </v-menu>
    </v-col>

    <v-col>
      <v-card
        flat
        class="px-4 pb-2"
      >
        <v-card-title class="py-7">
          Выводы: 1
        </v-card-title>
        <v-data-table
          :headers="headers"
          :items="items"
          :page.sync="page"
          :items-per-page="itemsPerPage"
          disable-sort
          no-data-text="Выводы отсутствуют"
          hide-default-footer
        >
          <template v-slot:top>
            <v-dialog
              v-model="dialog"
              persistent
              max-width="400px"
            >
              <v-card>
                <v-card-title>{{ dialogTitle }}</v-card-title>
                <v-card-text v-if="!accept">
                  <v-text-field
                    v-model="reason"
                    label="Причина"
                  />
                </v-card-text>
                <v-card-actions>
                  <v-spacer />

                  <v-btn
                    text
                    color="primary"
                    @click="dialog = false"
                  >
                    Отмена
                  </v-btn>
                  <v-btn
                    text
                    color="error"
                    @click="dialog = false"
                  >
                    {{ buttonText }}
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </template>

          <template v-slot:[`item.reason`]="{ item }">
            {{ item.reason || 'Отсутствует' }}
          </template>

          <template v-slot:[`item.createdAt`]="{ item }">
            {{ item.createdAt | moment('DD.MM.YYYY, HH:mm') }}
          </template>

          <template v-slot:[`item.action`]="{ item }">
            <v-btn
              depressed
              class="text-none ma-1"
              large
              height="36"
              @click.stop="denyWithdraw(item)"
            >
              Отклонить
            </v-btn>
            <v-btn
              depressed
              color="primary"
              class="text-none ma-1"
              large
              height="36"
              @click.stop="acceptWithdraw(item)"
            >
              Одобрить
            </v-btn>
          </template>
        </v-data-table>
        <v-divider />

        <div class="text-center pt-3">
          <v-pagination
            v-model="page"
            :total-visible="9"
            :length="Math.ceil(1 / itemsPerPage)"
          />

          <v-select
            v-model="itemsPerPage"
            :items="[5, 10, 15]"
            label="Выводов на странице"
            class="pa-4 pt-5"
            @change="firstPage"
          />
        </div>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
export default {
  data() {
    return {
      menu: false,
      dates: [],
      dateFrom: '',
      dateTo: '',
      headers: [
        { text: 'ID:', value: 'id', class: 'white--text text-subtitle-2' },
        { text: 'User ID:', value: 'userId', class: 'white--text text-subtitle-2' },
        { text: 'Сумма:', value: 'amount', class: 'white--text text-subtitle-2' },
        { text: 'Валюта:', value: 'currency', class: 'white--text text-subtitle-2' },
        { text: 'Способ оплаты:', value: 'payway', class: 'white--text text-subtitle-2' },
        { text: 'Статус:', value: 'status', class: 'white--text text-subtitle-2' },
        { text: 'Статус платежа:', value: 'paymentStatus', class: 'white--text text-subtitle-2' },
        { text: 'Причина отклонения:', value: 'reason', class: 'white--text text-subtitle-2' },
        { text: 'Дата создания:', value: 'createdAt', class: 'white--text text-subtitle-2' },
        { text: '', value: 'action', align: 'center' },
      ],
      page: 1,
      itemsPerPage: 5,
      dialog: false,
      accept: false,
      id: '',
      reason: '',
      items: [
        {
          id: 1,
          userId: 12,
          amount: 1050,
          currency: 'RUB',
          payway: 'QIWI',
          status: 'LOADING',
          paymentStatus: 'SUCCESS',
          reason: '',
          createdAt: 83271986445,
        },
      ],
    };
  },
  computed: {
    dateRangeText() {
      return this.dates.join(' ~ ');
    },
    dialogTitle() {
      return this.accept ? 'Одобрить заявку на вывод' : 'Отклонить заявку на вывод';
    },
    buttonText() {
      return this.accept ? 'Одобрить' : 'Отклонить';
    },
  },
  methods: {
    saveDates() {
      this.$refs.menu.save(this.dates);
      [this.dateFrom, this.dateTo] = this.dates;
    },
    denyWithdraw(item) {
      this.dialog = true;
      this.accept = false;
      this.id = item.id;
    },
    acceptWithdraw(item) {
      this.dialog = true;
      this.accept = true;
      this.id = item.id;
      this.reason = '';
    },
    firstPage() {
      this.page = 1;
    },
  },
};
</script>
